@mixin onTablet {
  @media (min-width: 768px) {
    @content;
  }
}

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin onBiggestDesktop {
  @media (min-width: 1200px) {
    @content;
  }
}

@mixin grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 16px;

  @media (min-width: 768px) {
    grid-template-columns: repeat(8, 1fr);
    column-gap: 24px;
  }
}

@mixin topActions {
  --icon-width: 18px;
  display: grid;
  grid-template-columns: 1fr var(--icon-width);
  align-items: center;
}
